oter {\n    position: relative;\n    color: $textLighterColor;\n    text-align: left;\n    padding: 0px 15px;\n    background-color: $bgColor;\n    min-height: 30px;\n    line-height: 30px;\n    z-index: 40;\n    border-radius: 0 0 4px 4px;\n    font-size: 12px;\n    overflow: hidden;\n\n    .is-small & {\n      padding: 3px 15px;\n      min-height: 25px;\n    line-height: 25px;\n    }\n\n    &-tz {\n      float: left;\n\n      &-picker {\n        display: inline-block;\n\n        &:hover &-select,\n        &-select:focus {\n          color: $textColor;\n          border-color: darken($borderColor, 10%);\n        }\n\n        &:hover &-arrowdown {\n          opacity: 0.6;\n        }\n\n        &-select {\n          appearance: none;\n          background: transparent;\n          font-size: inherit;\n          padding: 1px 20px 1px 5px;\n          transition: border 0.2s ease, color 0.2s ease;\n          border-radius: 3px;\n          border: 1px solid $borderColor;\n          color: $textLighterColor;\n          outline: none;\n          max-width: 220px;\n\n          &::-ms-expand {\n            display: none; // hide the default arrow in ie10 and ie11\n          }\n\n          .is-small & {\n            max-width: 180px;\n          }\n        }\n\n        &-arrowdown {\n          position: relative;\n          display: inline-block;\n          pointer-events: none;\n          opacity: 0.3;\n          left: -19px;\n          top: -2px;\n          transition: opacity 0.2s ease;\n\n          svg {\n            width: 8px;\n            height: 5px;\n          }\n        }\n      }\n\n      .bookingjs-timezoneicon {\n        height: 10px;\n        width: 10px;\n        margin-right: 5px;\n\n        .is-small & {\n          display: none;\n        }\n      }\n    }\n\n    &-by {\n      float: right;\n      transition: color 0.2s ease;\n      color: $textLighterColor;\n      text-decoration: none;\n      outline: none;\n\n      .bookingjs-timekitlogo {\n        width: 12px;\n        height: 12px;\n        margin-left: 5px;\n        top: 1px;\n        position: relative;\n\n        path {\n          transition: fill 0.2s ease;\n          fill: $textLighterColor;\n        }\n\n        .is-small & {\n          display: none;\n        }\n      }\n\n      &:hover,\n      &:focus {\n        color: $textColor;\n\n        .bookingjs-timekitlogo path {\n          fill: $textColor;\n        }\n      }\n\n      .is-small & {\n        float: left;\n      }\n    }\n  }\n\n  // Image avatar\n\n  &-avatar {\n    position: absolute;\n    top: -50px;\n    left: 50%;\n    transform: translateX(-50%);\n    border-radius: 150px;\n    border: 3px solid white;\n    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.13);\n    overflow: hidden;\n    z-index: 40;\n\n    .is-small & {\n      top: -40px;\n    }\n    background-color: white;\n\n    img {\n      max-width: 100%;\n      vertical-align: middle;\n      display: inline-block;\n      width: 80px;\n      height: 80px;\n\n      .is-small & {\n        width: 70px;\n        height: 70px;\n      }\n    }\n  }\n\n  &-displayname {\n    position: absolute;\n    top: 0;\n    left: 0;\n    padding: 15px 20px;\n    color: $textColor;\n    font-weight: 600;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    @media only screen and (max-width: 300px) {\n      width: 40%;\n    }\n    @media only screen and (min-width: 300px) {\n      width: 45%;\n    }\n    @media only screen and (min-width: 480px) {\n      width: 65%;\n    }\n    @media only screen and (min-width: 640px) {\n      width: 75%;\n    }\n\n    .is-small.has-avatar & {\n      top: 44px;\n      padding: 0px 20px;\n      text-align: center;\n      left: 0;\n      right: 0;\n      box-sizing: border-box;\n    }\n  }\n\n  // Book page\n\n  &-bookpage {\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    top: 0;\n    left: 0;\n    background-color: $bgColor;\n    z-index: 30;\n    opacity: 0;\n    transition: opacity 0.2s ease;\n    border-radius: 4px;\n    text-align: center;\n\n    &.show {\n      opacity: 1;\n    }\n\n    &-close {\n      position: absolute;\n      top: 0;\n      right: 0;\n      padding: 18px;\n      transition: opacity 0.2s ease;\n      opacity: 0.3;\n\n      &:hover {\n        opacity: 1;\n      }\n    }\n\n    &-header {\n      margin-bottom: 30px;\n    }\n\n    &-date,\n    h2 {\n      text-align: center;\n      font-size: 34px;\n      font-weight: 400;\n      margin-top: 50px;\n      margin-bottom: 10px;\n\n      .is-small & {\n        font-size: 27px;\n      }\n    }\n\n    &-time,\n    &-resource,\n    h3 {\n      text-align: center;\n      font-size: 17px;\n      font-weight: 400;\n      margin-bottom: 15px;\n      margin-top: 10px;\n\n      .is-small & {\n        font-size: 15px;\n        margin-bottom: 15px;\n      }\n    }\n\n    &-resource-prefix {\n      text-align: center;\n      font-size: 10px;\n      font-weight: 300;\n      display: inline-block;\n      position: relative;\n      &:before {\n        content: \"\";\n        display: block;\n        width: 40px;\n        height: 0;\n        border-top: 1px solid $borderColor;\n        position: absolute;\n        left: -45px;\n        top: 7px;\n      }\n      &:after {\n        content: \"\";\n        display: block;\n        width: 40px;\n        height: 0;\n        border-top: 1px solid $borderColor;\n        position: absolute;\n        right: -45px;\n        top: 7px;\n      }\n\n      .is-small & {\n        // font-size: 15px;\n        // margin-bottom: 35px;\n      }\n    }\n  }\n\n  &-closeicon {\n    height: 15px;\n    width: 15px;\n  }\n\n  // Book page form\n\n  &-form {\n    width: 350px;\n    margin: 0 auto;\n    text-align: left;\n    position: relative;\n\n    .is-small & {\n      width: 90%;\n    }\n\n    &-box {\n      overflow: hidden;\n      position: relative;\n      line-height: normal;\n      box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.1);\n    }\n\n    &-success-mess